.container {
  /* --container-max-width: 1200px; */
  --container-padding: 15px;
  --gutter: 30px;
  width: 100%;
  max-width: var(--container-max-width);
  margin: 0 auto;
  padding: 0 var(--container-padding);
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--container-padding) * -1);
  margin-right: calc(var(--container-padding) * -1);
}

.col {
  padding: 0 calc(var(--gutter) / 2);
}

.-cols-1 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 1));
}

.-cols-2 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 2));
}

.-cols-3 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 3));
}

.-cols-4 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 4));
}

.-cols-5 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 5));
}

.-cols-6 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 6));
}

.-cols-7 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 7));
}

.-cols-8 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 8));
}

.-cols-9 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 9));
}

.-cols-10 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 10));
}

.-cols-11 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 11));
}

.-cols-12 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 12));
}

:root {
  --color-black: #000;
  --color-white: #fff;
  --color-dark: #151515;
  --quill-gray: #e5e5e3;
  --color-gray: #858585;
  --color-tundora: #454545;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow-anchor: none;
  margin: 0;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 40px;
  max-width: 1920px;
  width: 100%;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-link {
  position: relative;
  font-size: 15px;
  color: var(--color-dark);
}

.main-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  opacity: 0;
  transition: opacity 0.15s;
}

.main-link:hover::before {
  opacity: 1;
  transition: opacity 0.15s;
}

.main-link--white {
  color: var(--color-white);
}

.container-narrow {
  max-width: 1406px;
}

.to-top {
  position: fixed;
  right: 2%;
  bottom: 5%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  padding: 14px 18px;
  width: 48px;
  height: 48px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transition: transform 0.3s;
}

.to-top:hover {
  transform: rotate(360deg);
  transition: transform 0.3s;
}

@font-face {
  font-family: "Jost";
  src: url("../fonts/Jost-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Jost";
  src: url("../fonts/Jost-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 36px;
}

.header__shop-nav {
  margin-left: 250px;
}

.logo {
  display: inline-block;
  width: 117px;
  height: 22px;
}

.logo .logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  fill: var(--color-dark);
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__item:not(:last-child) {
  margin-right: 25px;
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
}

.shop-nav__link--user {
  background: url("../../img/serch-user.png") no-repeat center/cover;
}

.cart {
  height: auto;
  width: auto;
}

.cart__icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url("../../img/serch-bag.png") no-repeat center/cover;
}

.cart__quantity {
  color: var(--color-white);
  background-color: var(--color-dark);
  height: 16px;
  display: inline-flex;
  padding: 0 5px;
  align-items: center;
  border-radius: 25px;
  font-weight: 700;
  font-size: 10px;
}

.serch-form__btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url("../../img/search-space.png") no-repeat center/cover;
}

.hero {
  position: relative;
  height: 100vh;
  min-height: 700px;
  background-color: var(--quill-gray);
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.05);
}

.hero__container {
  position: relative;
  height: 100%;
}

.hero__container picture {
  position: absolute;
  right: 277px;
  bottom: 0;
  display: block;
  max-height: 90%;
}

.hero__title {
  margin: 0;
  color: var(--color-dark);
  font-size: 72px;
  font-weight: 700;
  text-align: left;
  max-width: 540px;
  margin-bottom: 40px;
}

.hero__descr {
  font-size: 18px;
  font-weight: 500;
  text-align: left;
}

.hero__img {
  display: block;
  object-fit: cover;
}

.hero__content {
  padding: 0 260px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.banner-slider .swiper-slide {
  height: 100%;
}
